<template>
	<view class="box">
		<view class="order-top">
			<view class="title">{{statusList[order.Status]}}</view>
			<!-- <view class="time">请在 00:29:29 内支付，否则将自动取消</view> -->
		</view>
		<view class="user-information">
			<image src="@/static/img/icon_adress-s@3x.png" mode=""></image>
			<view class="information">
				<view class="name">{{order.Name}} {{order.Phone}}</view>
				<view class="address">{{order.Region}}{{order.Address}}</view>
			</view>
		</view>
		<view class="commodity">
			<view class="commodity-left">
				<image src="@/static/img/fanqie.png" mode=""></image>
				<view class="">
					<view class="name">{{order.SeedlingsName}}</view>
					<view class="unit">{{order.Unit}}</view>
				</view>
			</view>
			<view class="commodity-number">x{{order.Amount}}</view>
		</view>
		<view class="order-information">
			<view class="title">订单信息</view>
			<view class="order-copy">
				<view class="order">订单编号：{{order.OrderNo}}</view>
				<view class="copy" @click="handleCopy">复制</view>
			</view>
			<view class="order create">创建时间：{{order.CreateTime}}</view>
			<view class="order">发货时间：{{order.DeliveryTime}}</view>
		</view>
		<view class="bottom">
			<view class="cancel-order" @click="viewLogistics">查看物流</view>
			<view class="pay-now" @click="handleHarvest">确认收货</view>
		</view>
		<!-- <view class="logistics" v-if="LogisticsDialogShow">
			<logistics-dialog @onHide="handleHide"></logistics-dialog>
		</view> -->
		<uni-popup ref="popup" type="center">
			<logistics-dialog :order="logistic" @onHide="handleHide"></logistics-dialog>
		</uni-popup>
	</view>
</template>

<script>
	import LogisticsDialog from './components/logistics/index.vue'
	import {
		GetConfirmOrderHarvestAjax
	} from '@/apis/api/modules/user-center'
	export default {
		components: {
			LogisticsDialog
		},
		data() {
			return {
				statusList: ['待发货', '待收货', '待评价', '已完成'],
				orderNumber: 'E20191106201633024700003',
				order: '',
				logistic: {
					LogisticsCompany: '',
					LogisticsNo: ''
				}
			}
		},
		onLoad() {
			let harvestList = uni.getStorageSync("harvestList");
			let items = JSON.parse(harvestList);
			this.order = items
			this.logistic.LogisticsCompany = this.order.LogisticsCompany
			this.logistic.LogisticsNo = this.order.LogisticsNo
		},
		methods: {
			handleHarvest() {
				uni.showModal({
					// title: '提示',
					content: '是否确认收货？',
					success: async res => {
						if (!res.confirm) {
							return false
						}
						const result = await GetConfirmOrderHarvestAjax({
							OrderID: this.order.OrderID,
							token: true
						})
						uni.showToast({
							title: result.Result,
							icon: 'none'
						})
						setTimeout(() => {
							uni.navigateBack({
								delta: 1
							})
						}, 800)

					}
				});
			},
			viewLogistics() {
				this.$refs.popup.open()
			},
			handleHide(e) {
				this.$refs.popup.close()
			},
			/**
			 * 复制订单编号
			 */
			handleCopy() {
				uni.setClipboardData({
					data: this.orderNumber, //要被复制的内容
					success: () => { //复制成功的回调函数
						uni.showToast({ //提示
							icon: 'none',
							title: '复制成功'
						})
					}
				});
			}
		}
	}
</script>

<style scoped lang="scss">
	.box {
		min-height: 100%;
		background: #F7F7F7;
	}

	.order-top {
		padding: 30rpx;
		background: #00D08F;

		.title {
			font-size: 32rpx;
			font-weight: 500;
			color: #FFFFFF;
			line-height: 44rpx;
			padding-bottom: 10rpx;
		}

		.time {
			font-size: 24rpx;
			font-weight: 400;
			color: #FFFFFF;
			line-height: 34rpx;
		}
	}

	.user-information {
		padding: 30rpx;
		background: #fff;
		display: flex;
		justify-content: start;
		align-items: center;

		.name {
			font-size: 32rpx;
			font-weight: 500;
			color: #333333;
			line-height: 44rpx;
			padding-bottom: 10rpx;
		}

		.address {
			font-size: 32rpx;
			font-weight: 400;
			color: #333333;
			line-height: 44rpx;
		}

		image {
			width: 60rpx;
			height: 60rpx;
			margin-right: 20rpx;
		}
	}

	.commodity {
		margin-top: 30rpx;
		padding: 30rpx;
		display: flex;
		justify-content: space-between;
		align-items: flex-end;
		border-bottom: 2rpx solid #efefef;
		background: #fff;

		.commodity-left {
			display: flex;
			justify-content: start;

			.name {
				font-size: 28rpx;
				font-weight: 500;
				color: #333333;
				line-height: 40rpx;
			}

			.unit {
				padding-top: 74rpx;
				font-size: 32rpx;
				font-weight: 500;
				color: #FEB042;
				line-height: 44rpx;
			}

			image {
				width: 160rpx;
				height: 160rpx;
				border-radius: 10rpx;
				margin-right: 20rpx;
			}
		}
	}

	.order-information {
		padding: 30rpx;
		margin-top: 30rpx;
		background: #fff;

		.order {
			font-size: 24rpx;
			font-weight: 400;
			color: #666666;
			line-height: 34rpx;
		}

		.create {
			padding: 20rpx 0;
		}

		.title {
			font-size: 28rpx;
			font-weight: 500;
			color: #333333;
			line-height: 40rpx;
			padding-bottom: 20rpx;
		}

		.order-copy {
			display: flex;
			justify-content: space-between;

			.copy {
				font-size: 24rpx;
				font-weight: 400;
				color: #00D08F;
				line-height: 34rpx;
			}
		}
	}

	.bottom {
		width: 100%;
		box-sizing: border-box;
		position: absolute;
		bottom: 0;
		background: #fff;
		padding: 30rpx;
		display: flex;
		justify-content: flex-end;
		text-align: center;
		line-height: 58rpx;
		border-radius: 30rpx 30rpx 0rpx 0rpx;

		.cancel-order {
			width: 152rpx;
			height: 58rpx;
			border-radius: 29rpx;
			border: 2rpx solid #999999;
			font-size: 24rpx;
			font-weight: 400;
			color: #999999;
			margin-right: 20rpx;
		}

		.pay-now {
			width: 152rpx;
			height: 58rpx;
			border-radius: 29rpx;
			border: 2rpx solid #00D08F;
			font-size: 24rpx;
			font-weight: 400;
			color: #00D08F;
			line-height: 58rpx;
		}
	}

	.logistics {
		position: fixed;
		width: 100%;
		height: 100%;
		z-index: 10;
		top: 0;
		left: 0;
		background: rgba(0, 0, 0, 0.75);
	}
</style>
